@font-face {
  font-family: "Muller";
  font-style: normal;
  font-weight: 400;
  src: local("Muller light"), url("../fonts/mullerlight-webfont.woff2") format("woff2");
}

@font-face {
  font-family: "PlayfairDisplay";
  font-style: normal;
  font-weight: 700;
  src: local("PlayfairDisplay bold"), url("../fonts/playfair_display_bold-webfont.woff2") format("woff2");
}

@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 400;
  src: local("Raleway light"), url("../fonts/raleway-light-webfont.woff2") format("woff2");
}

@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 500;
  src: local("Raleway semibold"), url("../fonts/raleway-semibold-webfont.woff2") format("woff2");
}

@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 700;
  src: local("Raleway extrabold"), url("../fonts/raleway-extrabold-webfont.woff2") format("woff2");
}

body,
html {
  margin: 0;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

p {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

body {
  font-family: "Raleway", "Muller", "PlayfairDisplay", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 25px;
  color: #5e5e5e;
  background-color: #bbb;
  /* #fdfaf3 */
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}

.wrapper {
  max-width: 1170px;
  margin: 0 auto;
  padding: 0 95px;
}

.slider-button,
.button-arrow,
.play,
.menu {
  font-size: 0;
  cursor: pointer;
}

a {
  cursor: pointer;
  text-decoration: none;
  color: #7d7d7d;
  font-size: 18px;
  line-height: 30px;
}

.submit-orange {
  cursor: pointer;
}

.phone-number {
  font-family: "Muller";
}

.intro-text {
  color: #ffffff;
  font-size: 14px;
  line-height: 20px;
  margin-top: 26px;
  letter-spacing: 1px;
}

.phone-number {
  color: #252525;
}

.logo {
  font-size: 0;
}

.navigation-list {
  display: none;
}

h1 {
  font-family: "PlayfairDisplay";
  font-size: 63px;
  line-height: 58px;
  color: #ffffff;
  letter-spacing: 2px;
}

.button-hover,
.submit-orange {
  display: block;
  font-weight: 700;
  font-size: 15px;
  line-height: 16px;
  color: #ffffff;
  text-transform: uppercase;
}

.intro-column .button-hover {
  margin-top: 35px;
  border: 2px solid white;
  padding: 20px 78px 17px 28px;
  max-width: 278px;
  letter-spacing: 1px;
  position: relative;
}

.intro-column .button-hover::after {
  content: "";
  position: absolute;
  background: url("../img/Arrow-OutRight-icon.png") 0 no-repeat;
  height: 22px;
  width: 40px;
}

.service-column .button-hover {
  color: #00203a;
}

.headline {
  font-family: "PlayfairDisplay";
  color: #00203a;
  font-size: 45px;
}

.features-list .feature-item .feature-headline {
  font-family: "Raleway";
  font-weight: 500;
  font-size: 22px;
  color: #1a292f;
}

.current-ref,
.services-ref.current,
.service-column span {
  color: #00203a;
}

.service-headline {
  font-family: "PlayfairDisplay";
  font-weight: 400;
  font-size: 30px;
  color: #00203a;
}

.service-column span,
.service-text {
  font-size: 18px;
  line-height: 30px;
}

.video-text {
  font-family: "Raleway";
  font-size: 18px;
  color: #ffffff;
}

.steps-headline {
  font-weight: 500;
  font-size: 22px;
  line-height: 17px;
  color: #252525;
}

.steps-number {
  font-size: 12px;
}

.form-headline {
  font-family: "PlayfairDisplay";
  font-size: 45px;
  color: #ffffff;
}

.form-text,
.user-text,
.form-text2 {
  font-size: 14px;
  line-height: 20px;
  color: #ffffff;
}

.client-name {
  font-weight: 500;
  font-size: 22px;
  line-height: 30px;
  color: #252525;
}

.feedbacks-name {
  font-weight: 700;
  color: #efb172;
}

.sgn1,
.sgn2 {
  color: #ffc487;
}

.main-header {
  padding-top: 83px;
}

.main-header .wrapper {
  position: relative;
}

.slider-button,
.logo,
.phone {
  display: none;
}

.slider-box {
  position: relative;
}

.main-bg {
  position: absolute;
  top: -83px;
  left: -100px;
}

.intro-column {
  position: absolute;
  max-width: 325px;
  top: 170px;
  left: 190px;
}