body {
	font-family: "Raleway", "Muller", "PlayfairDisplay", "Arial", sans-serif;
	font-style: normal;
	font-weight: 400;
	font-size: 16px;
	line-height: 25px;
	color:@text-grey;
	background-color: @color-body;       /* #fdfaf3 */
}

html {
	box-sizing: border-box;
}

*, 
*::before, 
*::after {
	box-sizing: inherit;
}

.visually-hidden {
	position: absolute;
	clip: rect(0 0 0 0);
	width: 1px;
	height: 1px;
 	margin: -1px;
}

.wrapper {
	max-width: 1170px;
	margin: 0 auto;
	padding: 0 95px;
}

.slider-button,
.button-arrow,
.play,
.menu {
	font-size: 0;
	cursor: pointer;
}

a {
	cursor: pointer;
	text-decoration: none;
	color: @light-grey;
	font-size: 18px;
	line-height: 30px;
}

.submit-orange {
	cursor: pointer;
}

.phone-number {
	font-family: "Muller";
}

.intro-text {
	color: @color-white;
	font-size: 14px;
	line-height: 20px;
	margin-top: 26px;
	letter-spacing: 1px;
}

.phone-number {
	color: @phone-color;
}

.logo {
	font-size: 0;
}

.navigation-list {
	display: none;
}

h1 {
	font-family: "PlayfairDisplay"; 
	font-size: 63px;
	line-height: 58px;
	color: @color-white;
	letter-spacing: 2px;
}

.button-hover,
.submit-orange {
	display: block;
	font-weight: 700;
	font-size: 15px;
	line-height: 16px; 
	color: @color-white;
	text-transform: uppercase;
}

.intro-column {
	.button-hover {
		margin-top: 35px;
		border: 2px solid white;
		padding: 20px 78px 17px 28px;
		max-width: 278px;
		letter-spacing: 1px;
		position: relative;
		&::after {
			content: "";
			position: absolute;
			background: url("../img/Arrow-OutRight-icon.png") 0 no-repeat;
			height: 22px;
			width: 40px;
		}
	}
	
}

.service-column {
.button-hover {
		color: @headline-color; 
	}
}

.headline {
	font-family: "PlayfairDisplay"; 
	color: @headline-color;
	font-size: 45px;
}

.features-list {
	.feature-item {
		.feature-headline {
			font-family: "Raleway";
			font-weight: 500;
			font-size: 22px;
			color: @second-block;
		}
	}
}

.current-ref,
.services-ref.current,
.service-column span {
	color: @headline-color;
}

.service-headline {
	font-family: "PlayfairDisplay"; 
	font-weight: 400;
	font-size: 30px;
	color: @headline-color;
}

.service-column span,
.service-text {
	font-size: 18px;
	line-height: 30px;
}

.video-text {
	font-family: "Raleway";
	font-size: 18px;
	color: @color-white;
}

.steps-headline {
	font-weight: 500;
	font-size: 22px;
	line-height: 17px;
	color: @phone-color;
}

.steps-number {
	font-size: 12px;
}

.form-headline {
	font-family: "PlayfairDisplay"; 
	font-size: 45px;
	color: @color-white;
}

.form-text,
.user-text,
.form-text2 {
	font-size: 14px;
	line-height: 20px;
	color: @color-white;
}

.client-name {
	font-weight: 500;
	font-size: 22px;
	line-height: 30px;
	color: @phone-color;
}

.feedbacks-name {
	font-weight: 700;
	color: @light-orange;
}

.sgn1,
.sgn2 {
	color: @color-copyright;
}
